/* 多张图片 */
<style>
  /*.image-preview-modal {*/
  /*padding: 50px;*/
  /*}*/
  .image-preview-modal .image-border {
    height: 400px;
    position: relative;
    overflow: hidden;
    border: 1px solid #ddd;
  }
  .image-preview-modal .image-preview {
    height: 100%;
    width: auto;
    transition: all 0.6s;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    margin: 0 auto;
  }
</style>
<template>
  <Modal v-model="isShow" width="700" :footerHide="true" title="图片预览" class="image-preview-modal default">
    <Carousel v-model="value1" v-if="imageList.length !== 0" @on-change="changeImage">
      <CarouselItem  v-for="image in imageList" :key="image">
        <Row class="image-border">
          <img class="image-preview" :src="image" />
        </Row>
      </CarouselItem>
    </Carousel>
  </Modal>

</template>

<script>
  import common from '../libs/common';
  import server from '../config/api';
  import Vue from 'vue';

  export default {
    name: 'ImagePreview',
    // 子组件参数
    // fileList; 多张图片列表，形如 ['floor/123', 'floor/456']
    // imagePreviewModal: 是否显示modal，与父组件中的data双向绑定
    props: ['fileList', 'imagePreviewModal'],
    data() {
      return {
        value1: 0,
        common: common,
        isShow: false,
        imageList: [],
        scale: 1,
      }
    },
    methods: {
      closeModal: function () {
//        server.api.img + '?fileName=' + imgName;
        this.$emit('closeImagePreviewModal');
      },
      changeImage: function() {
        $(".image-preview").each(function() {
          $(this).css("left", "0");
          $(this).css("right", "0");
          $(this).css("top", "0");
          $(this).css("transform", "scale(1,1)");
          this.scale = 1;
        });
      },
      initImg: function() {
        let imagePreview = $(".image-preview");
        let self = this;
        imagePreview.on("mousewheel DOMMouseScroll", function (e) {
          let delta = (e.originalEvent.wheelDelta && (e.originalEvent.wheelDelta > 0 ? 1 : -1)) ||  // chrome & ie
            (e.originalEvent.detail && (e.originalEvent.detail > 0 ? -1 : 1));              // firefox
          if (delta <= 0) {
            // 向上滚 缩小
            if(self.scale <= 1) {
              return;
            }
            self.scale --;
            let width = parseInt($(this).css("width").replace("px", ""));
            let height = parseInt($(this).css("height").replace("px", ""));
            let left = parseInt($(this).css("left").replace("px", ""));
            let top = parseInt($(this).css("top").replace("px", ""));
            left += parseInt(e.offsetX) - width/2;
            top += parseInt(e.offsetY) - height/2;
            $(this).css("left", left+"px");
            $(this).css("top", top+"px");
            $(this).css("transform", "scale("+self.scale+","+self.scale+")");
          } else {
            // 向下滚 放大
            if(self.scale >= 5) {
              return;
            }
            self.scale ++;
            let width = parseInt($(this).css("width").replace("px", ""));
            let height = parseInt($(this).css("height").replace("px", ""));
            let left = parseInt($(this).css("left").replace("px", ""));
            let top = parseInt($(this).css("top").replace("px", ""));
            left -= parseInt(e.offsetX) - width/2;
            top -= parseInt(e.offsetY) - height/2;
            $(this).css("left", left+"px");
            $(this).css("top", top+"px");
            $(this).css("transform", "scale("+self.scale+","+self.scale+")");
          }
        });
//        for(let i in document.images)document.images[i].ondragstart= function() { return false;};
        // 鼠标拖拽
        let isMouseDown = false; //鼠标是否按下
        let mouseDownPosiX;
        let mouseDownPosiY;
        let InitPositionX;
        let InitPositionY;
        imagePreview.mousedown(function (e) {
          if (e.preventDefault) {
            e.preventDefault();
          }
          else {
            e.returnvalue = false;
          }
          //当鼠标按下时捕获鼠标位置以及对象的当前位置
          $(this).css("transition", "none");
          mouseDownPosiX = e.pageX;
          mouseDownPosiY = e.pageY;
          isMouseDown = true;
          InitPositionX = $(this).css("left").replace("px", "");
          InitPositionY = $(this).css("top").replace("px", "");
        }).mousemove(function (e) {
          //当鼠标按下并且移动时，首先判断鼠标是否在当前焦点，以及鼠标是否已经弹起，
          if (isMouseDown) {
            let tempX = parseInt(e.pageX) - parseInt(mouseDownPosiX) + parseInt(InitPositionX);
            let tempY = parseInt(e.pageY) - parseInt(mouseDownPosiY) + parseInt(InitPositionY);
            $(this).css("left", tempX + "px").css("top", tempY + "px");
          }
          //当鼠标弹起或者离开元素时，将鼠标弹起置为false，不移动对象
        }).mouseup(function () {
          $(this).css("transition", "all 0.6s");
          isMouseDown = false;
        }).mouseleave(function () {
          $(this).css("transition", "all 0.6s");
          isMouseDown = false;
        });
      }
    },
    watch: {
      isShow: function(val, oldVal){
        if (val) {
          // 处理图片数据
          for(let index in this.fileList) {
            this.imageList[index] = server.api.img.replace('{imageId}', this.fileList[index]);
          }
          Vue.nextTick(() => {
            // 添加图片监听事件
            this.initImg();
          });
        } else {
          // 如果是关闭了，需要给父组件传递消息，否则父组件的modal参数一直为true
          this.closeModal();
        }
      },
      imagePreviewModal: function (val, oldVal) {
        this.isShow = val;
      }
    }
  }

</script>



